body {
  height: 100vh;
  background: #dddddd;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Roboto',sans-serif;
}

.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Display {
  height: 4rem;
  width: 100%;
  background-color: gray;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0;
  margin: 0;
  border-radius: 1px;
}

.ButtonPanel {
 display: grid;
 grid-template-rows: repeat(4,1fr);
}

.Button {
  width: 70px;
  height: 70px;
}

.LastRow {
  color: red;
}